.list-item {
  margin: 5px 0;
  padding: 3px 5px;
  background: #eeeeee;
  cursor: pointer;
}
.list-item:first-of-type {
  margin-top: 0;
}
.list-item:last-of-type {
  margin-bottom: 0;
}
.list-item:hover {
  background: #d3d3ff;
}



